.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Navbar {
  height: 10vh;
  background-color: black;
  position:fixed;
  bottom: 0%;
  width: 100%;
  color: white;
}

.Chapter {
  height: 10vh;
  background-color: black;
  position:fixed;
  top: 11%;
  width: 100%;
  color: white;
}
.wrapper{
  width: 100%;
  height: 60vh;
  position: fixed;
  top: 22%;
  background-color: antiquewhite;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  
}
.wrapper::-webkit-scrollbar{
  width: 0;
}

.wrapper .item{
  width: 80vw;
  min-height: 70vh;
  background-color: white;
  margin-bottom: 10px;
  background-color: green;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
